.actions-toolbar {
    > .secondary {
        text-align: center;
        .action {
            @extend .abs-button-responsive;
            margin-bottom: $indent__s;
            &:last-child {
                margin-bottom: 0;
            }
        }
        margin-bottom: $indent__s;
        &:last-child {
            margin-bottom: 0;
        }
    }
}
